.gallery__container {
  background-image: linear-gradient(to bottom, #fff5f6 40%, #ffffff 60%);
}

.masonry__thumb img {
  width: 100%;
  height: 100%;
}

.gread__list {
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 4px;
}

@media screen and (min-width: 768px) {
  .gread__list {
    grid-gap: 8px;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(9, 1fr);
  }
}

.gread__item {
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.gread__item:hover {
  transform: rotate(1turn) scale(1.1);
}

.gread__item-1 {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 4;
}

@media screen and (min-width: 768px) {
  .gread__item-1 {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 4;
  }
}

